import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';
import shortId from 'shortid';

import './style.scss';
import IconButton from '../../../../components/buttons/IconButton';
import { ToggleButton } from '../../../../components/buttons/ToggleButton';
import { TOGGLE_BUTTON, SEPARATOR } from '../../../../types/ActionBarType';

const ToolSetBar = ({ tools, intl }) => (
  <div className="toolset-bar-wrapper">
    {tools &&
      tools.map((tool) => {
        if (tool && tool.type === SEPARATOR) {
          return (
            <div className="toolset-bar-separator" key={`separator_${shortId.generate()}`}>
              <div className="separator"></div>
            </div>
          );
        }
        if (tool && tool.type === TOGGLE_BUTTON) {
          return (
            <ToggleButton
              key={tool.id}
              id={tool.id}
              intl={intl}
              intlId={tool.intlId}
              iconUrl={tool.iconUrl}
              onClickHandler={tool.onClickHandler}
              intlIdAlternative={tool.intlIdAlternative}
              iconUrlAlternative={tool.iconUrlAlternative}
              onClickHandlerAlternative={tool.onClickHandlerAlternative}
              showAlternative={tool.showAlternative}
            />
          );
        }

        return (
          <IconButton
            key={tool.intlId}
            tooltip={intl.messages[`${tool.intlId}`]}
            onClick={() => {
              tool.onClickHandler();
            }}
            iconUrl={tool.iconUrl}
          />
        );
      })}
  </div>
);

ToolSetBar.defaultProps = {
  isToolAction: false,
};

ToolSetBar.propTypes = {
  tools: PropTypes.arrayOf(
    PropTypes.shape({
      intlId: PropTypes.string,
      onClickHandler: PropTypes.func,
      iconUrl: PropTypes.string,
    }),
  ).isRequired,
  isToolAction: PropTypes.bool,
};

export default injectIntl(ToolSetBar);
