import { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { Treebeard } from 'react-treebeard';

import iconArchive from '../../assets/icons/archive.svg';
import iconLinear from '../../assets/icons/arrow-top-right.svg';
import iconCollapse from '../../assets/icons/collapse-all.svg';
import iconCopy from '../../assets/icons/copy.svg';
import iconCut from '../../assets/icons/cut.svg';
import iconCylindricalWrap from '../../assets/icons/cylindrical-wrap.svg';
import iconDelete from '../../assets/icons/delete.svg';
import iconExpand from '../../assets/icons/expand-all.svg';
import iconExternal from '../../assets/icons/external.svg';
import iconGroup from '../../assets/icons/group.svg';
import iconImage from '../../assets/icons/image.svg';
import iconLoadImage from '../../assets/icons/load-image.svg';
import iconLoop from '../../assets/icons/loop.svg';
import iconOne from '../../assets/icons/one.svg';
import iconColor from '../../assets/icons/palette.svg';
import iconPaste from '../../assets/icons/paste.svg';
import iconPerspective from '../../assets/icons/perspective.svg';
import iconPolarTransformation from '../../assets/icons/polar-transformation.svg';
import iconResize from '../../assets/icons/resize.svg';
import iconShift from '../../assets/icons/shift.svg';
import iconStatistics from '../../assets/icons/statistics.svg';
import iconTableAnalyzer from '../../assets/icons/table-analyzer.svg';
import iconTable from '../../assets/icons/table.svg';
import iconTransformation from '../../assets/icons/transformation.svg';
// import iconUndoDisabled from '../../assets/icons/undo-disabled.svg';

// import iconUndo from '../../assets/icons/undo.svg';
import iconWindow from '../../assets/icons/window.svg';
import ActionBar from '../../components/ActionBar';
import ConfirmDialog from '../../components/ConfirmDialog';
import ProxyType from '../../components/Protobuf/ProxyType';
import Protobuf from '../../protobuf/Protobuf';
import { canReadToolSetView, canEditToolSetView } from '../../utils/permissions';
import ToolSetBar from './components/ToolSetBar';
import ToolSetup from './components/ToolSetup';
import TreebeardStyle from './components/TreebeardStyle';

import './style.scss';

const useStyles = makeStyles(() => ({
  label: {
    fontSize: '1.4rem',
    transform: 'translate(10px, 12px) scale(1)',
  },
}));

const ToolsetView = ({ inspectionId }) => {
  const proxy = useSelector((state) => state.app.proxy);
  const configuration = useSelector((state) => state.app.configuration);
  const myPermissions = useSelector((state) => state.app.myPermissions);
  const [treeModel, setTreeModel] = useState([]);
  const [filteredTreeModel, setFilteredTreeModel] = useState(undefined);
  const [cursor, setCursor] = useState({});
  const [lastActionsStack] = useState([]);
  const [toolSetId, setToolSetId] = useState(0);
  const [update, setUpdate] = useState(false);
  const [openConfirmDelete, setOpenConfirmDelete] = useState(false);
  const [filterText, setFilterText] = useState('');
  const classes = useStyles();

  useEffect(() => {
    if (configuration.inspectionToolsetMappings.size === 0) {
      configuration
        .setInspectionIdToToolsetMappings(proxy.protobuf)
        .then(() => proxy.dispatchCurrentConfiguration(configuration));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    const treeDataRaw = configuration.getConfigTreeByInspectionId(inspectionId);
    setToolSetId(treeDataRaw?.toolSetId);

    const createTreeNodes = (node, loop) => {
      const { name, isValid, position, objectID } = node;
      if (node.children && node.children.length > 0) {
        return {
          name,
          isValid,
          position,
          objectID,
          toggled: false,
          children: node.children.map(loop),
        };
      }
      return { name, isValid, position };
    };

    const traverseTree = () => {
      const loop = (node) => {
        if (!node) {
          return;
        }
        return createTreeNodes(node, loop);
      };

      return loop(treeDataRaw);
    };

    const tree = traverseTree();

    if (!tree || !tree.children) {
      return;
    }

    tree.children[0].active = true;
    setCursor(tree.children[0]);

    const treeModel = tree.children.sort(sortToolSet);
    setTreeModel(treeModel);
  }, [configuration, inspectionId]);

  const sortToolSet = (a, b) => {
    return a.position > b.position ? 1 : -1;
  };

  const onNodeClicked = (node, toggled) => {
    treeModel.forEach((treeNode) => {
      treeNode.active = false;
    });

    // eslint-disable-next-line
    node.active = true;

    if (node.children) {
      // eslint-disable-next-line
      node.toggled = toggled;
    }

    lastActionsStack.push({
      type: 'select',
      from: cursor.position,
      to: node.position,
    });

    setCursor(node);
    setUpdate(!update);
  };

  const setNext = () => {
    const { position } = cursor;
    treeModel.forEach((treeNode) => {
      if (treeNode.position === position + 1) {
        cursor.active = false;
        treeNode.active = true;
        setCursor(treeNode);
      }
    });
  };

  const deleteTool = async (objectId) => {
    const { position } = cursor;
    lastActionsStack.push({
      type: 'delete',
      tool: { ...cursor, active: false },
    });

    await proxy.deleteTool(inspectionId, objectId);
    setTreeModel(treeModel.filter((treeNode) => treeNode.position !== position));
    setNext();
  };

  const copyTool = async () => {
    const { objectID } = cursor;
    lastActionsStack.push({
      type: 'copy',
      tool: { ...cursor, active: false },
    });

    await proxy.copyTool(inspectionId, objectID);
  };

  const cutTool = async () => {
    const { position, objectID } = cursor;
    lastActionsStack.push({
      type: 'cut',
      tool: { ...cursor, active: false },
    });

    await proxy.cutTool(inspectionId, objectID);
    setTreeModel(treeModel.filter((treeNode) => treeNode.position !== position));
    setNext();
  };

  const pasteTool = async () => {
    const { objectID } = cursor;
    lastActionsStack.push({
      type: 'paste',
      tool: { ...cursor, active: false },
    });

    await proxy.pasteTool(inspectionId, objectID);
    setUpdate(!update);
  };

  // TODO: undo can only be shown if all actions can really be undone
  // const undoLastAction = () => {
  //   if (lastActionsStack.length === 0) {
  //     return;
  //   }

  //   const lastAction = lastActionsStack.pop();

  //   switch (lastAction.type) {
  //     case 'select':
  //       treeModel.forEach((treeNode) => {
  //         if (treeNode.position === lastAction.from) {
  //           cursor.active = false;
  //           treeNode.active = true;
  //           setCursor(treeNode);
  //         }
  //       });
  //       break;
  //     case 'delete':
  //       // treeModel.push(lastAction.tool);
  //       // setTreeModel(treeModel.sort(sortToolSet));
  //       // setPrevious();
  //       break;
  //     case 'add':
  //       deleteTool(lastAction.toolId);
  //       break;
  //     default:
  //   }
  // };

  const addTool = async (type) => {
    const { position } = cursor;
    const newToolId = await proxy.addTool(inspectionId, toolSetId, type, position);
    lastActionsStack.push({
      type: 'add',
      position,
      toolId: newToolId,
    });
  };

  const expandTools = () => {
    treeModel.forEach((treeNode) => {
      treeNode.toggled = true;
    });
    setUpdate(!update);
  };

  const collapseTools = () => {
    treeModel.forEach((treeNode) => {
      treeNode.toggled = false;
    });
    setUpdate(!update);
  };

  const handleConfirmDeleteClose = (confirmed) => {
    if (confirmed) {
      deleteTool(cursor.objectID);
    }
    setOpenConfirmDelete(false);
  };

  const getViewActions = () => {
    return [
      { intlId: 'copy', iconUrl: iconCopy, onClickHandler: () => copyTool() },
      { intlId: 'cut', iconUrl: iconCut, onClickHandler: () => cutTool() },
      { intlId: 'paste', iconUrl: iconPaste, onClickHandler: () => pasteTool() },
      { intlId: 'delete', iconUrl: iconDelete, onClickHandler: () => setOpenConfirmDelete(true) },
      // {
      //   intlId: 'undo',
      //   iconUrl: lastActionsStack.length === 0 ? iconUndoDisabled : iconUndo,
      //   onClickHandler: () => (lastActionsStack.length === 0 ? {} : undoLastAction()),
      // },
      { intlId: 'expandAll', iconUrl: iconExpand, onClickHandler: () => expandTools() },
      { intlId: 'collapseAll', iconUrl: iconCollapse, onClickHandler: () => collapseTools() },
    ];
  };
  const viewActions = getViewActions();

  const getToolActions = () => {
    return [
      {
        intlId: 'toolsetView.windowTool',
        iconUrl: iconWindow,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.WindowToolClassId),
      },
      {
        intlId: 'toolsetView.mathTool',
        iconUrl: iconOne,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.MathToolClassId),
      },
      {
        intlId: 'toolsetView.statisticsTool',
        iconUrl: iconStatistics,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.StatisticsToolClassId),
      },
      {
        intlId: 'toolsetView.ringBufferTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.RingBufferToolClassId),
      },
      {
        intlId: 'toolsetView.archiveTool',
        iconUrl: iconArchive,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ArchiveToolClassId),
      },
      {
        intlId: 'toolsetView.acquisitionTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.AcquisitionToolClassId),
      },
      {
        intlId: 'toolsetView.transformationTool',
        iconUrl: iconTransformation,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TransformationToolClassId),
      },
      {
        intlId: 'toolsetView.shiftTool',
        iconUrl: iconShift,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ShiftToolClassId),
      },
      {
        intlId: 'toolsetView.cylindricalWarpTool',
        iconUrl: iconCylindricalWrap,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.CylindricalWarpToolClassId),
      },
      {
        intlId: 'toolsetView.perspectiveTool',
        iconUrl: iconPerspective,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.PerspectiveToolClassId),
      },
      {
        intlId: 'toolsetView.imageTool',
        iconUrl: iconImage,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ImageToolClassId),
      },
      {
        intlId: 'toolsetView.linearTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LinearToolClassId),
      },
      {
        intlId: 'toolsetView.colorTool',
        iconUrl: iconColor,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ColorToolClassId),
      },
      {
        intlId: 'toolsetView.resizeTool',
        iconUrl: iconResize,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ResizeToolId),
      },
      {
        intlId: 'toolsetView.loadImageTool',
        iconUrl: iconLoadImage,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LoadImageToolClassId),
      },
      {
        intlId: 'toolsetView.tableTool',
        iconUrl: iconTable,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TableToolClassId),
      },
      {
        intlId: 'toolsetView.tableAnalyzerTool',
        iconUrl: iconTableAnalyzer,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TableAnalyzerToolClassId),
      },
      {
        intlId: 'toolsetView.polarTransformationTool',
        iconUrl: iconPolarTransformation,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.PolarTransformationToolClassId),
      },
      {
        intlId: 'toolsetView.loopTool',
        iconUrl: iconLoop,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LoopToolClassId),
      },
      {
        intlId: 'toolsetView.externalTool',
        iconUrl: iconExternal,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ExternalToolClassId),
      },
      {
        intlId: 'toolsetView.groupTool',
        iconUrl: iconGroup,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.GroupToolClassId),
      },
    ];
  };

  const toolActions = getToolActions();
  const onChangeFilter = (event) => {
    const text = event.target.value;

    if (text === '') {
      setFilteredTreeModel(undefined);
    }
    setFilterText(text);
    setFilteredTreeModel(treeModel.filter((item) => item.name.indexOf(text) > -1));
  };

  if (canReadToolSetView(myPermissions) || canEditToolSetView(myPermissions)) {
    return (
      <div className="toolset-wrapper">
        <ConfirmDialog
          open={openConfirmDelete}
          onClose={(confirmed) => handleConfirmDeleteClose(confirmed)}
          message={
            <FormattedMessage id="toolsetView.confirmDelete" values={{ toolName: cursor.name }} />
          }
        />
        <ToolSetBar tools={toolActions} />
        <div className="overflow-auto h-100 w-100">
          <div>
            <ToolSetup node={cursor} />
            <div className="w100">
              <TextField
                label="Filter"
                type="text"
                variant="outlined"
                style={{ width: '100%' }}
                onChange={onChangeFilter}
                value={filterText}
                InputLabelProps={{ classes: { root: classes.label } }}
              />
            </div>
            <Treebeard
              data={filteredTreeModel !== undefined ? filteredTreeModel : treeModel}
              onToggle={onNodeClicked}
              style={TreebeardStyle}
            />
          </div>
        </div>
        <ActionBar isViewAction actions={viewActions} />
      </div>
    );
  }
  return <div></div>;
};

ToolsetView.propTypes = {
  proxy: ProxyType,
  configuration: PropTypes.object,
};

export default ToolsetView;
